import { defHttp } from '/@/utils/http/axios';

enum Api {
  getOrgUrl = '/system/org/list-tree',
  getRelatedPartyUrl = '/baseset/related-party/get-list',

  getRegionNameUrl = '/baseset/common/list-region',

  saveStationUrl = '/baseset/powerstation/save',
  deleteStationUrl = '/baseset/powerstation/delete',
  getPowerStationUrl = '/baseset/powerstation/get',
  getPowerStaionListUrl = '/baseset/powerstation/page',

  exportPowerStaionUrl = '/baseset/powerstation/export-excel',

  updateBatchSortUrl = '/baseset/powerstation/updateBatchSort',

  saveOperationUrl = '/baseset/operation/save',
  deletedOperationUrl = '/baseset/operation/delete',
  getOperationListUrl = '/baseset/operation/list',

  savePeriodUrl = '/baseset/period/save',
  deletedPeriodUrl = '/baseset/period/delete',
  getPeriodListUrl = '/baseset/period/list',
  getPeriodtUrl = '/baseset/period/get',

  saveGridUrl = '/baseset/grid/save',
  deletedGridUrl = '/baseset/grid/delete',
  getGridListUrl = '/baseset/grid/list',

  doesItExistDayReprotUrl = '/po/day-report/exists/list',
}

export const getDoesItExistDayReprot = (params) =>
  defHttp.get({ url: Api.doesItExistDayReprotUrl, params }, {});

export const exportPowerStationData = (params) =>
  defHttp.download({ url: Api.exportPowerStaionUrl, params }, { isReturnNativeResponse: true });

//获取组织数据
export const getOrgData = (params) => defHttp.post({ url: Api.getOrgUrl, params }, {});
//获取往来单位
export const getRelatedParty = (params) =>
  defHttp.get({ url: Api.getRelatedPartyUrl, params }, { isOnlyResult: true });

export const getRegionNameData = () => defHttp.post({ url: Api.getRegionNameUrl }, {});

export const updateBatchSortData = (params) =>
  defHttp.post({ url: Api.updateBatchSortUrl, params }, { isOnlyResult: true });

//电站列表信息
export const getPowerStaionList = (params) =>
  defHttp.get({ url: Api.getPowerStaionListUrl, params }, { isOnlyResult: true });
//保存电站信息
export const savePowerStation = (params) => {
  return defHttp.post({ url: Api.saveStationUrl, params }, { isOnlyResult: true });
};
//删除电站信息
export const deletePowerStation = (id) =>
  defHttp.delete({ url: Api.deleteStationUrl + '?id=' + id }, {});
//查询电站基础信息
export const getPowerStation = (params) => defHttp.get({ url: Api.getPowerStationUrl, params }, {});

